	module.exports = {"data":{"markdownRemark":{"html":"<p>到中央党校去买书，那里的书店倒是不小，买了一百多元钱的书，有萨缪尔逊的《经济学》第12版，《未来的社会主义》等书。一本讲古印度哲学的书《顺世论》，想看看他们如何思考问题，在逻辑上和哲学观上有什么不同。印度哲学的思考问题的方法是十分不同的。毛姆的小说《刀锋》受到印度哲学很大影响。</p>\n<p>大家谈到了干部工资太低的问题。这样的话，中国的管理是不能搞好的，社会的优秀人才往哪里去，如果没有一个良性的机制，优秀的人才就不会往国家和党的管理中枢中流动，这样核心管理从长远来说就会发生问题。这是战略问题。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0083.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0083.md","frontmatter":{"title":"4月28日 星期四"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0083.md absPath of file >>> MarkdownRemark"}}

